/* The sidepanel menu */
.sidepanel {
  width: 250px;
  height: 100%;
  position: relative;
  float: left;
  z-index: 1;
  background-color: black;
  overflow-x: hidden;
  padding-top: 60px;
  transition: 0.5s;
  color: white;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  row-gap: 20px;
}

/* The sidepanel links */
.sidepanel a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 14px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.sidepanel a:hover {
  color: #f1f1f1;
}
.menuItemSidebar {
  opacity: 0.5;
  font-family: Risque;
  font-weight: normal;
  padding: 10px;
  font-size: 18px;
  line-height: 16px;
  font-style: italic;
  display: flex;
  align-items: center;
  text-align: center;
  color: #f8fa90;
}
.menuItemSidebarlower {
  opacity: 0.5;
  width: 50%;
  font-family: Risque;
  font-weight: normal;
  padding: 20px;
  font-size: 18px;
  line-height: 16px;
  font-style: italic;
  display: flex;
  align-items: center;
  text-align: center;
  color: #f8fa90;
}

.active {
  opacity: 1;
}
.horizontal {
  color: #f8fa90;
  width: 40%;
  background-color: #f8fa90;
}
.upperContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-bottom: 20px;
  padding-top: 30px;
}
.lowerContainer {
  display: flex;
