}
/* Position and style the close button (top right corner) */
.sidepanel .closebtn {
  position: absolute;
  top: 5px;
  right: 15px;
  font-size: 36px;
  margin-left: 50px;
  cursor: pointer;
}
.sidepanel .backbtn {
  position: absolute;
  top: 5px;
  left: 0px;
  font-size: 36px;
  cursor: pointer;
}
.sidepanel > div {
  display: block;
  cursor: pointer;
}

